.dse-select {
  display: flex;
  flex-direction: column;
  position: relative;
}

.dse-select__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border: 1px solid #d8d8d8;
  background: #ffffff;
  font-family: var(--dse-body-font-size, "Raleway", "HelveticaNeue", "Helvetica", "Arial", sans-serif);
  cursor: pointer;
}

.dse-select__overlay {
  padding: 0rem;
  margin: 0rem;
  border: 1px solid #d8d8d8;
  background: #ffffff;
  z-index: 99;
  position: absolute;
  width: 100%;
  box-shadow: 0px 4px 11px rgba(157, 157, 157, 0.21);
  list-style: none;
}

.dse-select__option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
  padding: 0.75rem;
  box-sizing: border-box;
}

.dse-select__option--highlighted {
  background-color: #d8d8d8;
}

.dse-select__option--selected {
  background-color: #5ece7b;
  color: #ffffff;
}

.dse-select__caret {
  transition: all 250ms ease;
}

.dse-select__caret--open {
  transform: rotate(180deg);
}

.dse-select__caret--close {
  transform: rotate(0deg);
}
